* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input{
  width: 100%;
  font-size: 1.1rem;
  color: gray;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin: 0 10px 10px 0;
}

.card p {
  padding: 5px;
  text-align: center;

}

.card img {
  max-height: 170px;
}